nav {
  width: 100%;
  padding: .5rem 0;
  box-shadow: 0 1px 3px #0000001a;
}

nav .max-w-7xl {
  width: 100%;
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (width >= 640px) {
  nav .max-w-7xl {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (width >= 1024px) {
  nav .max-w-7xl {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

nav .flex {
  display: flex;
}

nav .justify-between {
  justify-content: space-between;
}

nav .items-center {
  align-items: center;
}

nav .h-16 {
  height: 4rem;
}

nav .text-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

nav .font-bold {
  font-weight: 700;
}

nav .text-blue-400 {
  color: #60a5fa;
}

nav .hidden.md\:flex {
  display: none;
}

@media (width >= 768px) {
  nav .hidden.md\:flex {
    display: flex;
  }
}

nav .space-x-8 > * + * {
  margin-left: 2rem;
}

nav button {
  cursor: pointer;
  color: #d1d5db;
  background: none;
  border: none;
  padding: .5rem;
  font-size: 1rem;
  transition: color .2s;
}

nav button:hover, nav button.text-blue-400 {
  color: #60a5fa;
}

nav .md\:hidden {
  display: block;
}

@media (width >= 768px) {
  nav .md\:hidden {
    display: none;
  }
}

nav .text-white {
  color: #fff;
}

nav .h-6, nav .w-6 {
  width: 1.5rem;
  height: 1.5rem;
}

nav + div.md\:hidden {
  display: block;
}

@media (width >= 768px) {
  nav + div.md\:hidden {
    display: none;
  }
}

nav + div.md\:hidden .px-2 {
  padding-left: .5rem;
  padding-right: .5rem;
}

nav + div.md\:hidden .pt-2 {
  padding-top: .5rem;
}

nav + div.md\:hidden .pb-3 {
  padding-bottom: .75rem;
}

nav + div.md\:hidden .space-y-1 > * + * {
  margin-top: .25rem;
}

nav + div.md\:hidden button {
  text-align: left;
  color: #d1d5db;
  width: 100%;
  padding: .5rem .75rem;
  transition: color .2s;
  display: block;
}

nav + div.md\:hidden button:hover {
  color: #60a5fa;
}
